import { Directive, EventEmitter, Input, Output } from '@angular/core';

import { PresentationAmortizationPayment, PresentationAmortizationPaymentStatus } from '@backbase/data-ang/loans';

@Directive()
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export abstract class LoanAmortizedPaymentScheduleBaseComponent {
  /**
   * Amortize payment schedule items
   */
  @Input() items!: PresentationAmortizationPayment[];
  /**
   * Emits on row select
   */
  @Output() readonly selectItem = new EventEmitter<PresentationAmortizationPayment>();

  /**
   * Indicates the overdue state for amortized payment schedule item
   *
   * @param status
   */
  isOverdue({ status }: PresentationAmortizationPayment): boolean {
    return status === PresentationAmortizationPaymentStatus.Overdue;
  }

  /**
   * Track by func for NgFor directive
   *
   * @param _index
   * @param item
   */
  trackById(_index: number, item: PresentationAmortizationPayment): string {
    return item.id;
  }
}
